@use "global" as *;
.p-tsunagu-mv {
  padding-top: myrem(94);
  background: url(../images/tsumugi/sp_mv-back.jpg) no-repeat center center /
    cover;
  aspect-ratio: 375 / 661;

  @include mq("md") {
    padding-top: myrem(175);
    background: url(../images/tsumugi/mv-back.jpg) no-repeat center center /
      cover;
    aspect-ratio: 1280 / 661;
  }
}

.p-tsunagu-mv__inner {
  @include mq("md") {
    max-width: calc(myrem(1148) + $padding-pc * 2);
  }
}

.p-tsunagu-mv__lead {
  span {
    font-size: maxrem(17);
    letter-spacing: calc(-20 / 1000 * 1em);
    font-weight: 700;
    background-color: $white;
    color: $green-color;
    display: block;
    width: fit-content;
    padding: myrem(4);
    font-family: $font-helvetica;

    @include mq("md") {
      font-size: maxrem(28);
    }
  }
}

.p-tsunagu-mv__title {
  padding-top: myrem(16);
  font-size: maxrem(38);
  font-weight: 700;
  letter-spacing: calc(-20 / 1000 * 1em);
  color: $white;

  @include mq("md") {
    font-size: maxrem(70);
  }
}
