@use 'sass:math';

// インナー幅
$innerWidth: 1080px;

// 余白
$padding-pc: 25px;
$padding-sp: 20px;

// フォント
$font-main: "NotoSansJP", sans-serif;
$font-helvetica: "Helvetica", sans-serif;
$font-arial: "Arial Narrow Bold", sans-serif;
$fons-serif: "NotoSerifJP-Regular.otf", serif;

// 色指定
$black: #333;
$white: #fff;
$primary-color: #365864;
$green-color: #0b845e;

// ブレイクポイント
$breakpoints: (
  sm: 600,
  md: 768,
  lg: 1024,
  xl: 1440,
);

// メディアクエリ
$mediaquerys: (
  sm: "screen and (min-width: #{map-get($breakpoints,'sm')}px)",
  md: "screen and (min-width: #{map-get($breakpoints,'md')}px)",
  lg: "screen and (min-width: #{map-get($breakpoints,'lg')}px)",
  xl: "screen and (min-width: #{map-get($breakpoints,'xl')}px)",
);

// メディアクエリを "@include mq(){}" で書くことができる
@mixin mq($mediaquery: md) {
  @media #{map-get($mediaquerys, $mediaquery)} {
    @content;
  }
}

// px → remへと変換し、最小値12pxを設ける
@function maxrem($pixels) {
  @return max(math.div($pixels, 16) * 1rem, 12px);
  }