@use "global" as *;

.c-section-title {
  color: $primary-color;
  font-size: maxrem(30);
  font-weight: 700;
  line-height: calc(38 / 30);
  letter-spacing: 0;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: myrem(24);
  text-align: center;

  @include mq("md") {
    font-size: maxrem(36);
    line-height: calc(45 / 36);
  }

  &::before {
    position: absolute;
    content: "";
    background: url(../images/tsumugi/title-under-line.svg) no-repeat center
      center / contain;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    width: myrem(335);
    height: myrem(14);

    @include mq("md") {
      width: myrem(471);
      height: myrem(10);
    }
  }

  img {
    @include mq("md") {
      width: myrem(220);
    }
  }
}
