@use "global" as *;
.p-tsunagu-can {
  @include mq("md") {
    padding-top: myrem(78);
  }
}

.p-tsunagu-can__inner {
  @include mq("md") {
    max-width: calc(myrem(1190) + $padding-pc * 2);
  }
}

.p-tsunagu-can__list {
  padding-top: myrem(40);
}

.p-tsunagu-can__item {
  @include mq("md") {
    position: relative;
  }

  & + & {
    margin-top: myrem(40);
  }
  &:nth-child(odd) {
    .p-tsunagu-can__item-img {
      @include mq("md") {
        margin-left: auto;
      }
    }
  }
  &:nth-child(even) {
    .p-tsunagu-can__item-body {
      @include mq("md") {
        right: 0;
      }
    }
  }
}

.p-tsunagu-can__item-body {
  @include mq("md") {
    position: absolute;
    top: 0;
  }
}

.p-tsunagu-can__item-img {
  position: relative;
  width: 100%;

  @include mq("md") {
    width: myrem(787);
  }

  img {
    height: auto;
  }
}

.p-tsunagu-can__head {
  color: $white;
  font-size: maxrem(19);
  position: absolute;
  left: myrem(18);
  line-height: 1;
  bottom: myrem(-10);
  font-weight: 500;

  @include mq("md") {
    font-size: maxrem(28);
    color: $primary-color;
  }

  span {
    font-family: $font-helvetica;
    font-size: maxrem(63);
    letter-spacing: 0;
    font-weight: 700;

    @include mq("md") {
      font-size: maxrem(90);
      color: $white;
      -webkit-text-stroke: 2px $primary-color;
      text-stroke: 2px $primary-color;
    }
  }
}

.p-tsunagu-can__item-title {
  margin-top: myrem(32);
  span {
    display: block;
    width: fit-content;
    color: $white;
    font-weight: 700;
    font-size: maxrem(16);
    line-height: calc(34 / 16);
    letter-spacing: 0;
    padding: myrem(0) myrem(20) myrem(0) myrem(6);
    background: linear-gradient(
      0deg,
      rgba(65, 160, 108, 1) 0%,
      rgba(11, 132, 94, 1) 100%
    );

    @include mq("md") {
      font-size: maxrem(28);
      letter-spacing: calc(20 / 1000 * 1em);
    }
  }
  span + span {
    margin-top: myrem(5);
  }
}

.p-tsunagu-can__item-text {
  margin-top: myrem(10);

  @include mq("md") {
    background-color: $white;
    padding: myrem(30) myrem(23);
    font-size: maxrem(16);
    line-height: calc(28 / 16);
    width: myrem(595);
  }
}

.p-tsunagu-can__item-note {
  font-weight: 400;
  font-size: maxrem(11);
  letter-spacing: calc(50 / 1000 * 1em);

  @include mq("md") {
    font-size: maxrem(12);
    font-weight: 500;
  }
}

