@use "global" as *;

.p-tsunagu-support {
  padding-bottom: 0;
  background: linear-gradient(
    to right,
    rgba(64, 159, 108, 1) 0%,
    rgba(11, 132, 94, 1) 100%
  );

  @include mq("md") {
    padding-top: myrem(73);
  }
}

.p-tsunagu-support__text {
  font-size: maxrem(24);
  font-weight: 700;
  line-height: calc(34 / 24);
  letter-spacing: 0;
  color: $white;
  text-align: center;

  @include mq("md") {
    font-size: maxrem(34);
    line-height: calc(44 / 34);
    letter-spacing: calc(80 / 1000 * 1em);
  }
}

.p-tsunagu-support__list {
  margin-top: myrem(26);
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: myrem(15);

  @include mq("md") {
    margin-top: myrem(37);
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    gap: myrem(13);
  }
}

.p-tsunagu-support__item {
  font-weight: 700;
  font-size: maxrem(13);
  line-height: calc(20 / 13);
  padding: myrem(18) myrem(16);
  background-color: $white;
  color: $primary-color;
  border-radius: 13px;

  @include mq("md") {
    padding: myrem(28) myrem(24);
    font-size: maxrem(18);
    line-height: calc(28 / 18);
    letter-spacing: calc(-20 / 1000 * 1em);
  }
}

.p-tsunagu-support__img {
  margin-top: myrem(-30);

  @include mq("md") {
    margin-top: myrem(-40);
    width: myrem(493);
    margin-inline: auto;
  }
}
