@use "global" as *;
.p-footer {
  padding-top: myrem(18);
  padding-bottom: myrem(18);
  background: linear-gradient(to right, rgba(65,160,108,1) 0%, rgba(11,132,94,1) 100%);
  color: $white;
}

.p-footer__list {
  display: flex;
  gap: myrem(10);
justify-content: center;
}

.p-footer__link {
  font-size: maxrem(11);
  font-family: $fons-serif;
  letter-spacing: calc(50 / 1000 * 1em);
}

.p-footer__logo {
  margin-top: myrem(18);
  width: myrem(140);
  margin-inline: auto;
}

.p-footer__copy {
  margin-top: myrem(63);
  font-size: maxrem(11);
  font-weight: 400;
  text-align: center;
}
