@use "global" as *;

.p-tsunagu-faq {
  background-color: #f6f1e9;
}

.p-tsunagu-faq__list {
  margin-top: myrem(8);
}

.p-tsunagu-faq__item {
  padding-top: myrem(15);
  padding-bottom: myrem(15);
  color: #444;
  
  & + & {
    border-top: 1px solid #e6e6e6;
  }
}

.p-tsunagu-faq__question {
  position: relative;
  padding-top: myrem(5);
  padding-left: myrem(30);
  letter-spacing: 0;
  font-size: maxrem(16);
  font-weight: 700;

  &::before {
    position: absolute;
    content: "Q";
    left: 0;
    top: 0;
    width: fit-content;
    height: auto;
    font-family: $font-arial;
    font-size: maxrem(25);
    font-weight: 700;
    color: $primary-color;
  }
}

.p-tsunagu-faq__answer {
  margin-top: myrem(10);
  position: relative;
  padding-top: myrem(5);
  padding-left: myrem(30);
  letter-spacing: 0;
  font-size: maxrem(14);

  &::before {
    position: absolute;
    content: "A";
    left: 0;
    top: 0;
    width: fit-content;
    height: auto;
    font-family: $font-arial;
    font-size: maxrem(22);
    font-weight: 700;
    color: #ff0000;
  }
}
