@use "global" as *;

.p-tsunagu-how {
  padding-bottom: myrem(60);
}

.p-tsunagu-how__list {
  margin-top: myrem(40);
  display: grid;
  gap: myrem(20);
}

.p-tsunagu-how__item {
  padding: myrem(30);
  background-color: #eddc8a;
  border-radius: 13px;
}

.p-tsunagu-how__title {
  color: $green-color;
  font-size: maxrem(24);
  font-weight: 700;
  text-align: center;
}

.p-tsunagu-how__text {
  margin-top: myrem(28);
}

.p-tsunagu-how__img {
  margin-top: myrem(46);
}
