@use "global" as *;

// インナー幅＋余白
$breakpointInner: $innerWidth + strip-unit($padding-pc) * 2;

// ルートフォント設定
html {
  // ~ 375px
  @media (max-width: 375px) {
    font-size: vw(375, 16);
  }

  & {
    // 376px ~ 767px
    font-size: 100%;
  }

  // 768px ~ インナー幅＋余白
  @include mq(md) {
    font-size: vw(strip-unit($breakpointInner), 16);
  }

  // インナー幅＋余白 ~
  @media (min-width: $breakpointInner) {
    font-size: 100%;
  }
}

// フォント、カラー設定
body {
  font-family: $font-main;
  color: $black;
  font-size: maxrem(15);
  letter-spacing: calc(-20 / 1000 * 1em);
  line-height: calc(26 / 15);

  @include mq("md") {
    font-size: maxrem(16);
    letter-spacing: 0;
    line-height: calc(28 / 16);
  }
}

// ホバー
a,
button {
  @include mq(md) {
    transition: 0.3s;
    &:hover {
      opacity: 0.7;
      cursor: pointer;
    }
  }
}

//セクション
section {
  padding-top: myrem(50);
  padding-bottom: myrem(50);

  @include mq("md") {
    padding-top: myrem(80);
    padding-bottom: myrem(90);
  }
}
