@use "global" as *;

.p-tsunagu-can-function {
  padding-top: myrem(6);
  padding-bottom: myrem(60);
}

.p-tsunagu-can-function__title {
  color: $primary-color;
  font-weight: 700;
  font-size: maxrem(23);
  line-height: calc(30 / 23);
  text-align: center;
}

.p-tsunagu-can-function__list {
  margin-top: myrem(20);
  gap: myrem(6);
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}

.p-tsunagu-can-function__item {
  border-radius: 5px;
  border: 1px solid $black;
  background-color: #EDDC8A;
  padding: myrem(5);
  text-align: center;
  font-size: maxrem(12);
  letter-spacing: 0;
  font-weight: 400;
}
