@use "global" as *;
.p-tsunagu-about {
  background-color: #f6f1e9;

  @include mq("md") {
    padding-top: myrem(140);
    padding-bottom: myrem(90);
    position: relative;
    overflow: hidden;

    &::before {
      position: absolute;
      content: "";
      background: url(../images/tsumugi/mv-left.png) no-repeat center center /
        contain;
      top: myrem(-60);
      left: calc(50% - myrem(670));
      width: myrem(483);
      height: myrem(524);
    }

    &::after {
      position: absolute;
      content: "";
      background: url(../images/tsumugi/mv-right.png) no-repeat center center /
        contain;
      top: myrem(53);
      right: calc(50% - myrem(610));
      width: myrem(301);
      height: myrem(402);
    }
  }
}

.p-tsunagu-about__title {
}

.p-tsunagu-about__text {
  margin-top: myrem(38);
  margin-top: myrem(30);
}

.p-tsunagu-about__img {
  margin-top: myrem(18);

  @include mq("md") {
    display: none;
  }
}

.p-tsunagu-about__content {
  margin-top: myrem(26);

  @include mq("md") {
    margin-top: myrem(150);
    display: grid;
    gap: myrem(53);
    grid-template-columns: myrem(540) myrem(487);
  }
}

.p-tsunagu-about__green-title {
  color: $green-color;
  font-weight: 700;
  font-size: maxrem(18);

  @include mq("md") {
    font-size: maxrem(30);
    line-height: calc(48 / 33);
    letter-spacing: calc(-20 / 1000 * 1em);
  }
}

.p-tsunagu-about__green-text {
  margin-top: myrem(20);
  font-weight: 400;
}

.p-tsunagu-about__img02 {
  margin-top: myrem(33);
}
