@use "global" as *;

.p-tsunagu-worry {
  position: relative;

  &::before {
    position: absolute;
    content: "";
    background: $white;
    left: 50%;
    bottom: 0;
    transform: translate(-50%,100%);
    width: myrem(90);
    height: myrem(40);
    clip-path: polygon(50% 100%,100% 0,0 0);
  }
}

.p-tsunagu-worry__list {
  margin-top: myrem(32);
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: myrem(15);

  @include mq("md") {
    margin-top: myrem(47);
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    gap: myrem(13);
  }
}

.p-tsunagu-worry__item {
  padding: myrem(18) 0 myrem(8);
  border: 1px solid $green-color;
  border-radius: 10px;

  @include mq("md") {
    padding: myrem(20);
  }

  &:nth-child(1) {
    .p-tsunagu-worry__img {
      width: myrem(88);

      @include mq("md") {
        width: myrem(160);
      }
    }
  }
  &:nth-child(2) {
    .p-tsunagu-worry__img {
      width: myrem(153);

      @include mq("md") {
        width: myrem(245);
      }
    }
  }
  &:nth-child(3) {
    .p-tsunagu-worry__img {
      width: myrem(99);

      @include mq("md") {
        width: myrem(155);
      }
    }
  }
  &:nth-child(4) {
    .p-tsunagu-worry__img {
      width: myrem(103);

      @include mq("md") {
        width: myrem(132);
      }
    }
  }
}

.p-tsunagu-worry__text {
  color: $green-color;
  font-size: maxrem(15);
  line-height: calc(17 / 15);
  text-align: center;
  font-weight: 700;

  @include mq("md") {
    font-size: maxrem(18);
    line-height: calc(24 / 18);
    letter-spacing: calc(-20 / 1000 * 1em);
  }
}

.p-tsunagu-worry__img {
  margin-top: myrem(10);
  margin-inline: auto;
}
